// FONTS
%font-big-text {
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  line-height: 85%;
  letter-spacing: -0.05em;
  text-transform: uppercase;
}

%font-body-text {
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: -0.01em;
}

%font-body-text-uppercase {
  font-family: $font-family;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

%font-h1 {
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 85%;
  letter-spacing: -0.05em;
  text-transform: uppercase;

  @include onTablet {
    font-size: 80px;
  }

  @include onDesktop {
    font-size: 144px;
  }
}

%font-h2 {
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 85%;
  letter-spacing: -0.05em;
  text-transform: uppercase;

  @include onTablet {
    font-size: 56px;
  }

  @include onDesktop {
    font-size: 72px;
  }
}

// ICONS & IMAGES
%background-image-contain {
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
}

%background-image-cover {
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}
